import { Component, OnInit, OnDestroy, PLATFORM_ID, Inject, QueryList, ViewChildren } from "@angular/core";

import { HttpClient } from "@angular/common/http";
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ThemePalette } from '@angular/material/core';

import { DomSanitizer } from '@angular/platform-browser';
import { MatBottomSheet, MatBottomSheetRef, MAT_BOTTOM_SHEET_DATA } from '@angular/material/bottom-sheet';
import {
  MatCarouselSlideComponent,
  Orientation
} from '@ngbmodule/material-carousel';

export interface ChipColor {
  id: string;
  name: string;

  color: ThemePalette;
  state: boolean;
}
export interface NewsData {
  title: string;
  topImage: string;
  content: any;
}

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']

})
export class HomeComponent implements OnInit {
  news: any = [];
  brnews: any = [];
  state: boolean | undefined;
  availableColors: ChipColor[] = [
    { id: "10000", name: '‡§∏‡§∞‡§ó‡•Å‡§ú‡§æ', color: 'primary', state: false },
    { id: "10001", name: '‡§≠‡§æ‡§∞‡§§', color: 'warn', state: false },
    // { id: "10002", name: '‡§µ‡§ø‡§¶‡•á‡§∂', color: 'primary', state: false },
    { id: "10003", name: '‡§Æ‡§®‡•ã‡§∞‡§Ç‡§ú‡§®', color: 'warn', state: false },
    // { id: "10004", name: '‡§ó‡§∞‡§Æ ‡§µ‡•Ä‡§°‡§ø‡§Ø‡•ã', color: 'primary', state: false },
    { id: "10005", name: '‡§∏‡§∞‡§ï‡§æ‡§∞‡•Ä ‡§®‡•å‡§ï‡§∞‡•Ä', color: 'warn', state: false }
    // { id: "10006", name: 'tweets', color: 'primary', state: false }

  ];
  constructor(private httpClient: HttpClient, public dialog: MatDialog,
    private _sanitizer: DomSanitizer, private _bottomSheet: MatBottomSheet,
    @Inject(PLATFORM_ID) private platformId: Object) { }
  selected: string | undefined;
  category: string | undefined;
  safeURL: any;
  ngOnInit() {
    this.httpClient.get("assets/newsfeed.json").subscribe(data => {
      this.news = data;
    });
    this.httpClient.get("assets/newsfeed_breaking.json").subscribe(data => {
      this.brnews = data;
    })
  }

  changeSelected(value: string) {

    this.news = [];
    this.category = value;

    if (this.category == "10000") {

      this.httpClient.get("assets/newsfeed.json").subscribe(data => {
        this.news = data;

      })

    }
    else if (this.category == "10002") {

      this.httpClient.get("assets/newsfeed_world.json").subscribe(data => {
        this.news = data;

      })

    }
    else if (this.category == "10001") {
      this.httpClient.get("assets/newsfeed_india.json").subscribe(data => {
        this.news = data;

      })
    }
    else if (this.category == "10003") {

      this.httpClient.get("assets/newsfeed_entertainment.json").subscribe(data => {
        this.news = data;
      })


    }
    else if (this.category == "10006") {
      this.httpClient.get("assets/newsfeed_tweets.json").subscribe(data => {
        this.news = data;
      })
    }

  }
	searchArticles(source){
		console.log("selected source is: "+source);
		// this.newsapi.getArticlesByID(source).subscribe(data => this.mArticles = data['articles']);
  }
  
  openBottomSheet(post: any): void {
    this._bottomSheet.open(BottomSheetOverviewExampleSheet, {
      data: {
        title: post.title,
        topImage: post.urlToImage,
        content: post.content
      }
    });
  }

  public slidesList = new Array<never>(2);
  public showContent = false;

  public parentHeight = 'auto';
  public timings = '250ms ease-in';
  public autoplay = true;
  public interval = 5000;
  public loop = true;
  public hideArrows = false;
  public hideIndicators = false;
  public color: ThemePalette = 'warn';
  public maxWidth = 'auto';
  public maintainAspectRatio = false;
  public proportion = 100;
  public slideHeight = '200px';
  public slides = this.slidesList.length;
  public overlayColor = '#00000040';
  public hideOverlay = false;
  public useKeyboard = true;
  public useMouseWheel = false;
  public orientation: Orientation = 'ltr';
  public log: string[] = [];

  @ViewChildren(MatCarouselSlideComponent) public carouselSlides: QueryList<
    MatCarouselSlideComponent
  >;

  public onChange(index: number) {
    this.log.push(`MatCarousel#change emitted with index ${index}`);
  }

}



@Component({
  selector: 'bottom-sheet-overview-example-sheet',
  templateUrl: 'bottom-sheet-overview-example-sheet.html'
})
export class BottomSheetOverviewExampleSheet {

  constructor(private _bottomSheetRef: MatBottomSheetRef<BottomSheetOverviewExampleSheet>, @Inject(MAT_BOTTOM_SHEET_DATA) public data: any) { }

  openLink(event: MouseEvent): void {
    this._bottomSheetRef.dismiss();
    event.preventDefault();
  }
}